<script>
	import Multiselect from "vue-multiselect";
	import {
		FormWizard,
		TabContent
	} from "vue-form-wizard";
	import axios from 'axios';
	import Layout from "../../layouts/main";
	import PageHeader from "@/components/page-header";
	/**
	 * written by 张文彧
	 */
	export default {
		components: {
			Layout,
			PageHeader,
			FormWizard,
			TabContent,
			Multiselect
		},
		data() {
			return {
				pid:'',
				form: {
					userId: '',
					hostIp: '',
					hostMac: '',
					hostUserName: '',
					hostUserPassword: '',
					hostUerPort: '22',
					hostUserProtocol: 'SSL',
				},
				veryCode: '123',
				loading: '',
				isAllFill: false,
				leaderName: '',
				leaderClass: '',
				title: "添加主机",
				items: [{
						text: "主机管理"
					},
					{
						text: "添加主机",
						active: true
					}
				],
				value: null,
				value1: null,
				options: [
					"Touchscreen",
					"Call Function",
					"Notifications",
					"Fitness",
					"Outdoor"
				],
				dropzoneOptions: {
					url: "https://httpbin.org/post",
					thumbnailWidth: 150,
					maxFilesize: 0.5,
					headers: {
						"My-Awesome-Header": "header value"
					}
				}
			};
		},
		/**
		 * 判断表单是否填写完整
		 * departmentName: '',
					description: '',
					leaderId: ''
		 */
		watch: {
			/**
			 * 监听FORM
			 */
			form: {
				handler() {
					/**
					 * userId: '',
						hostIp: '',
						hostMac: '',
						hostUserName:'',
						hostUserPassword:'',
						hostUerPort:'22',
						hostUserProtocol:'SSL',
					 */
					this.isAllFill = this.form.hostIp != '' && this.form.hostMac != '' && this.form.hostUserName != '' &&
						this.form.hostUserPassword != '' && this.form.hostUserPort != '' && this.form.hostUserProtocol !=
						''

				},
				deep: true
			}
		},
		methods: {
			/**
			 * 提交表单前验证表单是否填完
			 */
			beforeTabSwitch: function() {

				if (this.isAllFill) {
					return true;
				} else {
					this.$message({
						type: 'warning',
						message: '请填写所有必填项'
					});
					return false
				}

			},
			/**
			 * 开启连接进度条
			 */

			openFullScreen() {
				this.loading = this.$loading({
					lock: true,
					text: '尝试连接',
					background: 'rgba(0, 0, 0, 0.7)'
				});

			},
			/**
			 * 根据信息连接
			 */
			onComplete: function() {
				// alert('Yay. Done!');
				// /departments/insert
					let form2 = this.form;
					this.openFullScreen(),

					form2['pid'] =this.veryCode,
					form2['userId'] = this.userId,
					// console.log("aaa",form2.pid,form2.userId),
					axios
					.post('http://82.156.8.5:8848/hostregister/successornot', form2)
					.then(response => (
						// this.userList = response.data.list
						console.log(response),
						// this.$message({
						// 	type: 'success',
						// 	// message: '添加成功!'
						// })
						setTimeout(() => {
							this.loading.close(),
								this.$message({
									type: 'success',
									message: '添加成功!'
								}),
								this.$router.push('/hostList')
						}, 2000)
					))
					.catch(function(error) { // 请求失败处理
						console.log(error);
					});

			},
		},
		created() {
			this.userId = sessionStorage.getItem('id'),
				this.veryCode = sessionStorage.getItem('pid'),
				console.log(this.veryCode)
		}
	};
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" />
		<div class="row">
			<div class="col-lg-12">
				<div class="card">
					<div class="card-body">
						<form-wizard color="#5664d2" @on-complete="onComplete">
							<tab-content title="主机信息" :before-change="beforeTabSwitch">
								<div class="tab-pane" id="basic-info">
									<h4 class="card-title">填写主机信息</h4>
									<p class="card-title-desc">请填入以下所有基本信息</p>
									<form>
										<div class="form-group">
											<label for="productname">主机IP</label>*
											<input v-model="form.hostIp" id="productname" name="productname" type="text"
												class="form-control" />
										</div>
										<div class="form-group">
											<label for="productname">主机MAC</label>*
											<input v-model="form.hostMac" id="productname" name="productname"
												type="text" class="form-control" />
										</div>
										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label for="productname">主机用户名</label>*
													<input v-model="form.hostUserName" id="productname"
														name="productname" type="text" class="form-control" />
												</div>
											</div>
										</div>

										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label class="control-label">主机密码</label>*
													<input v-model="form.hostUserPassword" id="productname"
														name="productname" type="password" class="form-control" />
												</div>
											</div>
										</div>
										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label class="control-label">主机端口</label>*
													<multiselect v-model="form.hostUerPort" :options="['22', '22']">
													</multiselect>
												</div>
											</div>
										</div>
										<div class="row">
											<div class="col-md-6">
												<div class="form-group">
													<label class="control-label">主机协议</label>*
													<multiselect v-model="form.hostUserProtocol"
														:options="['SSL', 'SSL']">
													</multiselect>
												</div>
											</div>
										</div>

									</form>
								</div>
							</tab-content>
							<tab-content title="确认连接" style="margin: 3%;">
								<div class="tab-pane" id="product-img" style="color:#9494b8;">
									<h1 style="margin-left: 15%;">您的验证码为：</h1>
									<h1 style="margin-left: 38%;">{{this.veryCode}}</h1>
									<h1 style="margin-left: 50%;">请在客户端输入验证码</h1>
								</div>
							</tab-content>
							<el-button type="primary" plain slot="prev">返回</el-button>
							<el-button type="primary" plain slot="next">下一步</el-button>
							<el-button type="primary" plain slot="finish">提交申请</el-button>
						</form-wizard>
					</div>
				</div>
			</div>
		</div>
	</Layout>
</template>
